













































































.masthead.segment {
    min-height: 700px;
    padding: 1em 0em;
}

.masthead .ui.menu .ui.button {
    margin-left: 0.2em;
}

.masthead h3.ui.header {
    margin-top: 2em;
    margin-bottom: 1em;
    font-size: 3em;
    font-weight: normal;
}


.footer.segment {
    padding: 5em 0em;
}

.secondary.pointing.menu .toc.item {
    display: none;
}

@media only screen and (max-width: 700px) {
 
    .secondary.pointing.menu .toc.item {
        display: block;
    }
    .masthead.segment {
        min-height: 350px;
    }
    .masthead h3.ui.header {
        font-size: 2em;
        margin-top: 1.5em;
    }

}



